.main {
    padding: 20px;
    height: 100vh;
    display: flex; /* Add this to make the sidebar and content layout horizontally */
  }
  
  .sidebar {
    background-color: hwb(180 0% 67%);
    border-radius: 5px;
    box-shadow: 0 0 2px ghostwhite;
    margin-right: 20px;
    min-height: 100%;
    padding: 10px;
    width: 250px; /* Add a fixed width to the sidebar */
  }
  
  .logo {
    height: 15vh;
    width: 100%;
    margin-bottom: 20px;
    align-self: flex-start;
  }
  
  .header {
    background-color: white;
    border-radius: 5px;
    box-shadow: 0 0 2px gray;
    margin-bottom: 20px;
    height: 10vh;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  
  .content {
    width: 100%;
    height: 100%;
    overflow-y: auto; /* Add this to make the content area scrollable */
  }
  
  .body {
    background-color: white;
    border-radius: 5px;
    box-shadow: 0 0 2px gray;
    height: 82vh;
    width: 100%;
    padding: 15px;
    overflow-y: auto !important;
    overflow-x: scroll !important;
  }
  
  .menu {
    margin-top: 100px;
  }
  
  .menu-item {
    margin-top: 30px;
  }
  
  .menu-item a {
    color: #ffffffc0;
    text-decoration: none;
    font-size: 20px;
  }
  
  .menu-item i {
    color: #ffffffc0;
    text-decoration: none;
    font-size: 18px;
    margin: 0 15px;
  }
  
  .active-menu-item {
    color: white;
    background-color: #013737;
    padding: 5px;
    border-radius: 5px;
  }
  
  .collapsed-sidebar {
    background-color: hwb(180 0% 67%);
    border-radius: 5px;
    box-shadow: 0 0 2px ghostwhite;
    margin-right: 20px;
    min-height: 100%;
    padding: 10px;
  }
  
  .bell-icon {
    font-size: 30px;
    color: black !important;
    cursor: pointer;
    padding-left: 10px;
  }
  
  /* Add media queries to adjust the layout for smaller screens */
  
  @media only screen and (max-width: 768px) {
    .main {
      flex-direction: column; /* Change the layout to vertical on smaller screens */
    }
  
    .sidebar {
      width: 100%; /* Make the sidebar full-width on smaller screens */
      margin-right: 0;
    }
  
    .content {
      width: 100%;
      height: auto; /* Make the content area full-height on smaller screens */
    }
  }